











import Vue from "vue";

type DataType = {
  firstName: string;
  lastName: string;
};

export default Vue.extend({
  data(): DataType {
    return {
      firstName: "",
      lastName: "",
    };
  },
  name: "FullName",
  computed: {
    fullName(): string {
      return this.firstName + " " + this.lastName;
    },
  },
});
